import React,{Component} from "react";
import { Jumbotron, Container} from 'reactstrap';
import {Badge, Nav , Input , Modal, ModalHeader,NavItem,Button,
       Row , Col, InputGroup , InputGroupAddon,Table} from 'reactstrap'
import toastr from "toastr";
//services
import userServices from "../services/userServices";

import logo from "../logo.png"
import avatar from "../images/user_avatar.svg"


class App extends Component{
  constructor(props){
    super(props);
    this.state = {
      serveur : localStorage.getItem('server')? localStorage.getItem('server'):null,
      categories:[],
      articles : [],
      selected_categorie:null,
      //shopping list 
      shopping_list_modal : false,
      //commande
      commande : [],
      servers:[],
    };

    this.delete_meal_from_commande = this.delete_meal_from_commande.bind(this);
    this.switch_categorie = this.switch_categorie.bind(this);
    this.render_categories = this.render_categories.bind(this);
    this.add_article = this.add_article.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.shopping_list_toggler = this.shopping_list_toggler.bind(this);
    this.render_shopping_list = this.render_shopping_list.bind(this);
    this.add_commande = this.add_commande.bind(this);
    this.calculate_total_price = this.calculate_total_price.bind(this);
    this.add_qte = this.add_qte.bind(this);
    this.reduce_qte = this.reduce_qte.bind(this);
    this.add_qte_shopping_list = this.add_qte_shopping_list.bind(this);
    this.reduce_qte_shopping_list = this.reduce_qte_shopping_list.bind(this);
    this.check_commande = this.check_commande.bind(this);
    this.save_server = this.save_server.bind(this);
  };

  clear_inputs(){
    const inputs = document.getElementsByTagName("input");
    for (let i = 0; i < inputs.length; i++) {
      inputs[i].value = 1;
    }
  }
  // API calls
  componentDidMount(){
    //set categories
    userServices.get_categories()
    .then((categories_res)=>{
    if(categories_res.data){
      //set articles 
      userServices.get_articles()
      .then((articles_res)=>{
        this.setState({
          categories         : categories_res.data,
          selected_categorie :categories_res.data[0],
          articles           : articles_res.data
        });
      });
    }else{
      alert('ERROR')
    }
    
  });

  userServices.get_servers()
  .then((res)=>{
  this.setState({
    servers : res.data
  })

  })

  }

  //serveur view
  render_servers(){
    const view = this.state.servers.map((server,id)=>{
      return(
        <option key={id} value={id+1}>{server}</option>
      )
    })
    return view
  }

  // Switch between Categories
  switch_categorie(categorie){
    this.clear_inputs();
    this.setState({
      selected_categorie :categorie,
    })
  }

  // Side Nav view
  render_categories(){
    const view = this.state.categories.map((cat,index)=>{
      return(
        <NavItem key={index}>
          {this.state.selected_categorie === cat ? 
          (
            <Button  color="danger" block>{cat}</Button>
          )
          :
          (
            <Button outline color="danger" block onClick={()=>{this.switch_categorie(cat)}}> {cat} </Button>
          )
          }
        </NavItem>
      )
    })
    return view
  }
  
  //select all text in input 
  select(input_index){
    const input = document.getElementById(input_index);
    input.focus();
    input.select();
  }

  save_server(e){
    const select = document.getElementById('login_select')
    if(select.value != 0){
     localStorage.setItem('server',select.options[select.options.selectedIndex].text)
     this.setState({
      serveur : select.options[select.options.selectedIndex].text
     })
    }else{alert("nooo")
    }
  }

  
  //this will run when user change Qte input after shopping list
  handleInputChange(e) {
    const target = e.target;
    if(target.name === "QteShoppingList"){
      let list = this.state.commande;
      //get object index from  : 
      const index = target.id[15];
      let object = list[index]
      //modifer lobjet 
      object.Quantite = target.value ;
      //update state
      list[index] = object;
      //set state 
      this.setState({
        commande : list
      })
    }
  }

  add_qte_shopping_list(object_id , value){
    let list = this.state.commande;
    let object = list[object_id];
    let new_value = parseInt(object.Quantite) + parseInt(value);
    if(new_value < 1){
      new_value = 1;
    }
    object.Quantite = new_value
    list[object_id] = object;
    this.setState({
      commande:list
    })
  }
  reduce_qte_shopping_list(object_id , value){
    let list = this.state.commande;
    let object = list[object_id];
    const new_value = object.Quantite - value;
    if (new_value > 0){
      object.Quantite = new_value
      list[object_id] = object;
      this.setState({
        commande:list
      })
    }
  }

  
  // this will calculate total price 
  calculate_total_price(){
    const articles = this.state.commande;
    let price = 0 ;
    for (let i = 0; i < articles.length; i++) {
      let article = articles[i];
      if(article.Quantite > 0){
        price += parseInt(article.PrixVente) * parseInt(article.Quantite);
      }
    };
    return price
  }


  add_qte(input_id,value){
    let input = document.getElementById(input_id)
    let input_value = input.value
    if (!input_value){
      input_value += 1;
    }
    let new_value = parseInt(input_value) + parseInt(value)
    input.value = new_value
  }

  reduce_qte(input_id,value){
    let input = document.getElementById(input_id)
    let new_value = parseInt(input.value)+0 - parseInt(value)
    if(new_value > 0){
      input.value = new_value
    }
  }


  // this will add the selected article to the commande list 
  add_article(input_id , article){
    //get qte by searching the input
    let input = document.getElementById(input_id)
    let qte = input.value
    input.value = 1;
    if(!qte){
      qte = 1;
    }

    const new_article = {
      'id':article.id,
      'Categorie':article.Categorie,
      'Designation':article.Designation,
      'Quantite' : qte,
      'PrixVente' : article.PrixVente
    }

    let list = this.state.commande;
    list.push(new_article);
    this.setState({
      commande: list,
    })
    //create a toast
    this.toast("L'article a été ajouté à la carte","primary");
  }

  //dynamic dishes by default is pizza
  render_articles(){
    //get articles of selected categorie
  let articles = []; 
  for (let i = 0; i < this.state.articles.length; i++) {
    const article = this.state.articles[i];
    if(article.Categorie === this.state.selected_categorie){
      articles.push(article)
    }
  }

  const view = articles.map((article,index)=>{
    return( 
        <tr key={index}>
          <td className="col-sm-1 article-col">{article.Designation}</td>
          <td className="col-sm-1 article-col">{article.PrixVente} Dzd</td>
          <td className="col-sm-1 article-col">
            <InputGroup className="qte-btns">
              <InputGroupAddon addonType="prepend"><Button color="danger" size="sm" onClick={()=>this.reduce_qte("qte"+index,1)}><i className="fa fa-minus" aria-hidden="true"></i></Button></InputGroupAddon>
              <Input
              className="input_qte"
                  id={"qte"+index}
                  name="qte"
                  type="number"
                  min="1"
                  max="20"
                  step="1"
                  placeholder = "1"
                  onClick={()=>this.select("qte"+index)}
               />
              <InputGroupAddon addonType="append"><Button color="success" size="sm" onClick={()=>this.add_qte("qte"+index,1)}><i className="fa fa-plus" aria-hidden="true"></i></Button></InputGroupAddon>
              <InputGroupAddon addonType="append" className="ml-3"><Button color="primary" size="sm" onClick={()=>{this.add_article("qte"+index , article)}} ><i className="fa fa-cart-arrow-down" aria-hidden="true"></i></Button></InputGroupAddon>
            </InputGroup>
            
          </td>

        </tr>
    )
  })
  return view
  }


//Shoping list
shopping_list_toggler(){
  this.setState({
    shopping_list_modal : !this.state.shopping_list_modal,
  })
}

delete_meal_from_commande(index){
  //get the commande list
  let list = this.state.commande;
  //serach the item
  list.splice(index,1)
  //set state 
  this.setState({
    commande : list
  })
}


// Shopping list view
render_shopping_list(){
  const articles = this.state.commande;
  if(articles.length > 0){
    let view = articles.map((article,index)=>{
    
    return(
    
    <tr key={article.id+""+index} id={article.id}>
        <td className="p-0 pr-2"> 
          <button onClick={()=>this.delete_meal_from_commande(index)} type="button" className="close" aria-label="Close">
              <span aria-hidden="true"><i className="fa fa-trash-o" aria-hidden="true"></i>
            </span>
          </button>
        </td>
        <td className="col-sm-1 article-col">{article.Designation}</td>
        <td className="col-sm-1 article-col">{article.PrixVente} Dzd</td>
        <td className="col-sm-1 article-col text-center">
          <InputGroup className="qte-btns justify-content-center">
            <InputGroupAddon addonType="prepend"><Button color="danger" size="sm" onClick={()=>this.reduce_qte_shopping_list(index , 1)}><i className="fa fa-minus" aria-hidden="true"></i></Button></InputGroupAddon>
            <Input
            className="input_qte"
                id={"QteShoppingList"+index}
                name="QteShoppingList"
                type="number"
                min="1"
                max="20"
                step="1"
                value = {article.Quantite}
                onChange = {this.handleInputChange}
                onClick={()=>this.select("QteShoppingList"+index)}
              />
            <InputGroupAddon addonType="append"><Button color="success" size="sm" onClick={()=>this.add_qte_shopping_list(index,1)}><i className="fa fa-plus" aria-hidden="true"></i></Button></InputGroupAddon>
          </InputGroup>
        </td>

        {(() => 
          {  
            if(article.Quantite > 0){
              if(article.valid === 0){
                setTimeout(() => {
                  this.blink(article.id , "normal")
                }, 100);
              }
              return(  
                <td className="col-sm-1 article-col ">
                    {article.PrixVente * article.Quantite} Dzd
                </td>
              )
            }else{
              return(
                <td className="col-sm-1 article-col text-danger error-notification">Erreur</td>
              )
            }
          }
        )()}
      </tr>
    )
  })
  // add total price :
  return(view)
  }
  else{
    return(
      <></>
    )
  }
}


blink(article_id,type){
  if(type==="error"){
    document.getElementById(article_id).style.backgroundColor = "#ffb4ad";    
  }
  if(type==="warning"){
    document.getElementById(article_id).style.backgroundColor = "#ed9555";
  }
  if(type==="normal"){
    document.getElementById(article_id).style.backgroundColor = "";
  }
}

//Map commande list and add notification to each article
check_commande(){
  /*
  valid = 0 => Error Qte
  valid = 1 => Warning No longer ...
  valid = 2 => Success
  valid = 3 => danger Table number
  */
  const articles = this.state.commande;

  for (let i = 0; i < articles.length; i++) {
    const article = articles[i];
    if(parseInt(article.Quantite) > 0){
      const check_data = new FormData();
      
      check_data.append('article',article.Designation);

      userServices.check_article(check_data)
      .then((res)=>{
        if(res.data === 0){
          article['valid']= 1;
        }else{
          article['valid']= 2;
        }
        });
    }else{
      article['valid'] = 0
    }
  }
  return articles
}


add_commande(){
  //disable button :
  const button = document.getElementById("cmd-btn");
  button.disabled = true;

  let articles = this.check_commande()

  //Time out to wait the promise finish
  
  setTimeout(()=>{
    let commander = true
    /* Check the table num */
    const table_num = document.getElementById('table-num').value;
    const is_table_num_valid = Number.isInteger(parseInt(table_num)) && parseInt(table_num)>=0 ?(true):(false);
    
    if(! is_table_num_valid){
      this.toast("Vérifiez le numero de la table SVP","error");
      commander = false;
    }

    for (let i = 0; i < articles.length; i++) {
      if(articles[i].valid === 0){
        this.toast("Quantité invalide pour "+articles[i].Designation,"error");
        commander = false;
        //this to change view "red for error"
        this.blink(articles[i].id,"error");
      }
      if(articles[i].valid === 1){
        this.toast("Rupture de stock '"+articles[i].Designation+"'","warning")
        commander = false;
        //this to change view "red for error"
        this.blink(articles[i].id,"warning");
      }
    }

    

    // activate Buuton
    button.disabled = false;

    if(commander){
      //commande
      for (let i = 0; i < articles.length; i++) {
        const article = articles[i];
        const data = new FormData();
        
        data.append("serveur",this.state.serveur);
        data.append("tablenum",table_num)
        data.append("article",article.Designation);
        data.append("qte",article.Quantite);

        userServices.commande(data)
        .then((res)=>{
        })
      }

      //clear state
      this.setState({
        commande :[],
      })

      //toggle shopping list
      this.shopping_list_toggler();
      
      this.toast("votre commande a été envoyée avec succès","success")
    }
  },500)
}

//Notifications
toast(message , type){
  toastr.options = {
    "closeButton": false,
    "debug": false,
    "newestOnTop": false,
    "progressBar": false,
    "positionClass": "toast-top-right",
    "preventDuplicates": false,
    "onclick": null,
    "showDuration": "200",
    "hideDuration": "600",
    "timeOut": "2000",
    "extendedTimeOut": "1000",
    "showEasing": "swing",
    "hideEasing": "linear",
    "showMethod": "fadeIn",
    "hideMethod": "fadeOut"
  }

  switch (type) {
    case "error":
      toastr.error(message)
      break;
    
    case "success":
      toastr.success(message)
      break;
    
    case "warning":
      toastr.warning(message)
      break;
    
    case "primary":
      toastr.info(message)
      break;
  
    default:
      break;
  }

}

  //VIEW
render(){
    if(!this.state.serveur){
      return(
        <>
          <div className="container">
            <div className="d-flex justify-content-center h-100">
              <div className="user_card">
               
                <div className="d-flex justify-content-center">

                  <div className="brand_logo_container">
                    <img src={avatar} className="img-fluid brand_logo" alt=""/>
                  </div>
                </div>

                        <div className="d-flex flex-column align-items-center">

                            <div className="text-white">
                                <h1>Login</h1>
                            </div>

                            <div className="w-75">
                                <select id="login_select" className="form-select form-select-lg mb-3" aria-label=".form-select-lg example">
                                    <option key={-1} value="0">who are u ?</option>
                                    {this.render_servers()}
                                </select>
                            </div>
                        </div>


                        <div className="w-50" id="error_style">

                                <button id="login_btn" onClick={this.save_server} className="btn btn-lg w-100">Next</button>                        
                        
                        </div>
              </div>
            </div>
        </div>

      </>
      )
    }else{
      return(
        <>
        <Container fluid>
  
        <Row className="p-0">
  
          <Col xs="2" sm="2" className="side-nav-col">
                <h4 className="side-nav-title custom-font-1">Menu</h4>
                <Nav
                  vertical
                  className="side-nav">
                  {/*Categories*/}
                  {this.render_categories()}
              </Nav>
              
          </Col>
  
          <Col xs={{size:10,offset:2}} sm={{size:10,offset:2}}  className="dash">
          <Jumbotron fluid className="w-100" > 
              <Container fluid>
                <h3 className=" jumbotron-title text-center custom-font-1">Serveur : {this.state.serveur}</h3>
                <img id="logo" src={logo} className="" width="50" />
                <p id="copy">YOUINNOVATE &copy; tous les droits réservés </p>
            </Container>
          </Jumbotron>
        
          <div className="table-responsive">
                <Table>
                  <thead>
                    <tr>
                      <th className="col-sm-1 article-col">Article</th>
                      <th className="col-sm-1 article-col">Prix.U</th>
                      <th className="col-sm-1 article-col" id="qte">Qte</th>
                    </tr>
                  </thead>
                  <tbody>
                  {this.render_articles()}
                  </tbody>
              </Table>
          </div>
          </Col>
  
        </Row>
  
        </Container>
        
       
  
        {/* Shopping LIst Modal */}
        <Modal className='course-details-modal modal-xl' isOpen={this.state.shopping_list_modal} toggle={this.shopping_list_toggler} >
            <ModalHeader toggle={this.shopping_list_toggler}>Shopping List</ModalHeader>
            {this.state.shopping_list_modal ?
                (
                    <>
                      
                      {this.state.commande.length > 0 ? 
                      (
                        <>
                            <div className="table-responsive ml-0">
                          <Table className="ml-0">
                            <thead>
                            <tr>
                              <th className="col-sm-1 article-col"></th>
                              <th className="col-sm-1 article-col">Article</th>
                              <th className="col-sm-1 article-col">Prix.U</th>
                              <th className="col-sm-1 article-col text-center">Qte</th>
                              <th className="col-sm-1 article-col">Prix.T</th>
                            </tr>
                          </thead>
                          <tbody>
                          {this.render_shopping_list()}
                        </tbody>
                        </Table>
                        <div id="table-form" className="mt-2 mb-3 py-3 border-top border-bottom">
                          <h4 className="text-center">Numero de la table </h4>
                          <input className="form-control w-50 mx-auto" id="table-num" type="number" placeholder="Tab-num"/>
  
                        </div>
  
                        </div>
                        <Row className="shopping-list-bottom">
                        <Col xs="8">
                          <h5>Total Price = {this.calculate_total_price()} Dzd</h5>
                        </Col>
                        <Col xs="4" className="text-right">
                              <button id="cmd-btn" type="button" className="btn btn-sm btn-success" onClick={this.add_commande} >
                              commander 
                              </button>
                          </Col>
                      </Row>
                      </>
                
                      ):
                      (
                        <span className="no-articles">No Articles</span>
                      )
                    }
                      
                      
                  </>    
                ) :
                (<></>)}
        </Modal>
  
            <div className="shopping">
            {this.state.commande.length > 0 ?
            (<Badge color="dabger">{this.state.commande.length}</Badge>)
            :
            (<></>)}
            
            <button onClick={this.shopping_list_toggler} type="button" className="btn btn-success shopping_btn">
              <i className="fa fa-shopping-cart" aria-hidden="true"></i>
            </button>
            </div>
             </>
      )
    }

  }

}
export default App;