import axios from "axios";
import HeaderConfig from "./header.config";

const server_ip = "192.168.1.10"
const folder_name = "elkhaima_fly"
const server_name = "server"

class UserServices{

    //Get servers 
    get_servers(){
        return axios.get('http://'+ server_ip +'/'+folder_name+'/'+server_name+'/get_servers.php',{headers : HeaderConfig.jsonConfig()})
        .then((res)=>{
            return res
        })
    }
    
    //Get categories
    get_categories(){
        return axios.get('http://'+ server_ip +'/'+folder_name+'/'+server_name+'/get_categories.php',{headers : HeaderConfig.jsonConfig()})
        .then((res)=>{
            return  res
        })
    }

    //GET articles
    get_articles(){
        return axios.get('http://'+ server_ip +'/'+folder_name+'/'+server_name+'/get_articles.php',{headers : HeaderConfig.jsonConfig()})
        .then((res)=>{
            return res
        })
    }
    
    //pass commande
    commande(data){
        return axios.post('http://'+ server_ip +'/'+folder_name+'/'+server_name+'/commande.php',data ,{headers : HeaderConfig.formDataConfig()})
        .then((res)=>{
            //this res can be error
            return res
        })
    }

    check_article(data){
        return axios.post('http://'+ server_ip +'/'+folder_name+'/'+server_name+'/check_article.php',data,{headers : HeaderConfig.formDataConfig()})
        .then((res)=>{
            //this res can be error
            return res
        })
    }
    
}


export default new UserServices();